import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams } from 'ionic-angular';
import { ModalBottomPage } from '../modal-bottom/modal-bottom';

/**
 * Generated class for the LifestyleInquiryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lifestyle-inquiry',
  templateUrl: 'lifestyle-inquiry.html',
})
export class LifestyleInquiryPage {

  fetchData: boolean = true;
  constructor(public navCtrl: NavController, public modalCtrl: ModalController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LifestyleInquiryPage');
    setTimeout(() => {
      this.fetchData = false;
    }, 3000);
  }

  OpenModel() {
    const presentModel = this.modalCtrl.create("CvvPage",{
      componentProps: {
        title: 'Billing Address',
        type: 'billing',
      },
      showBackdrop: true,
      mode: 'ios',
      cssClass: 'modal-bg',
      enableBackdropDismiss: false,
    });

    presentModel.present();
  }

}



